body{
  background: #000 url('images/bg.jpg')
}

.grid { 
  color: white;
}

.container {
  max-width: 1100px;
  margin: auto;
  padding: 0 20px;
}

.img {
  width: 100%;
  height: 400px;
  object-fit: cover;
}

.img:hover  {
  transform: rotateY(180deg);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

header {
  height: 200px;
}

header img {
  width: 200px;
}

.cards {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1rem;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

